const default_state = [
  {
    icon: 'https://www.contentshortcuts.com/wp-content/uploads/2015/07/avatarcircle-150x150.png',
    title: '',
    description: '',
  },
  {
    icon: 'https://www.contentshortcuts.com/wp-content/uploads/2015/07/avatarcircle-150x150.png',
    title: '',
    description: '',
  },
  {
    icon: 'https://www.contentshortcuts.com/wp-content/uploads/2015/07/avatarcircle-150x150.png',
    title: '',
    description: '',
  },
]
export default function partners(state = default_state, action) {
  switch (action.type) {
    case 'BOOSTRAP_PARTNERS':
      return Object.assign({}, state);
    default:
      return state;
  }
}



